.app {
  display: flex;
  flex-direction: column;
}

.content {
  margin-left: 20vw;
  width: 80vw;
  height: 100vh;
}

.navbar {
  background-color: rgb(213, 188, 161);
  padding: 10px;
  position: fixed;
  top: 0;
  z-index: 10;
  display: flex;  
  flex-direction: column;
  align-items: flex-start;
  height: 100vh;
  width: 20vw;
  box-sizing: border-box;
}

.profile-container {
  width: 100%; 
  display: flex; 
  justify-content: center; 
  margin-bottom: 20px; 
}

.navbar a {
  margin-left: 20px;     
  text-decoration: none;  
  color: #000;   
  font-family: "Anton", sans-serif;
  font-size: 20px;  
  font-weight: 400;
}

.navbar custom { 
  font-family: "Inconsolata", monospace;
  letter-spacing: 1px;
  font-size: 18px;   
  font-weight: 600;
}

.contact-link {
  margin-top: auto; 
  margin-left: 20px; 
  text-decoration: none; 
  font-family: "Inconsolata", monospace; 
}

.content-section {
  position: fixed;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgb(235, 232, 219);
  padding-left: 20px;
  width: 80vw;
}

.content-subsection {
  align-items: left;
}

.content-section-main {
  position: fixed;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  background-color: rgb(235, 232, 219);
  padding-left: 20px;
  padding-right: 20px;
  width: 80vw;
}

.content-section-main h1 {
  font-family: "Didact Gothic", sans-serif;
  font-size: 80px;
  position: relative;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0;
}

.content-section-main h2 {
  font-family: "Didact Gothic", sans-serif;
  font-size: 40px;
  position: relative;
  font-weight: 500;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0;
}

.content-section-main h3 {
  font-family: "Didact Gothic", sans-serif;
  font-size: 30px;
  position: relative;
  font-weight: 800;
  padding-left: 20px;
  padding-right: 20px;
  margin-top: 5vh;
  margin-bottom: 0;
}
.content-section-main h4 {
  font-family: "Didact Gothic", sans-serif;
  font-size: 20px;
  position: relative;
  font-weight: 500;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 5px;
  margin: 0;
}

.content-section-main body {
  font-family: "Didact Gothic", sans-serif;
  font-size: 20px;
  position: relative;
  font-weight: 500;
  padding-left: 20px;
  padding-right: 20px;
  margin-top: 20px;
}

.content-section h1 {
  font-family: "Didact Gothic", sans-serif;
  font-size: 100px;
  position: relative;
  align-items: flex-start;
}

.content-section h1::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -10px; 
  width: 100%; 
  height: 2px; 
  border-bottom: 10px dotted #F567;
}

.content-section h2 {
  font-family: "Didact Gothic", sans-serif;
  font-size: 20px;
  position: relative;
  font-weight: 500;
  padding: 20px;
}

.content-section h2 strong {
  font-weight: 600;
}
.projects-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #7c1717;
}

.projects-card h1 {
  font-family: "Didact Gothic", sans-serif;
  font-size: 100px;
  position: relative;
  align-items: flex-start;
}

.projects-section {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  background-color: rgb(235, 232, 219);
  padding-left: 20px;
  padding-right: 20px;
  width: 80vw;
}

.projects-section h1 {
  font-family: "Didact Gothic", sans-serif;
  font-size: 80px;
  position: relative;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0;
}

.projects-section h2 {
  font-family: "Didact Gothic", sans-serif;
  font-size: 40px;
  position: relative;
  font-weight: 500;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0;
}

.projects-section h3 {
  font-family: "Didact Gothic", sans-serif;
  font-size: 30px;
  position: relative;
  font-weight: 800;
  padding-left: 20px;
  padding-right: 20px;
  margin-top: 5vh;
  margin-bottom: 0;
}
.projects-section h4 {
  font-family: "Didact Gothic", sans-serif;
  font-size: 20px;
  position: relative;
  font-weight: 500;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 5px;
  margin: 0;
}

.header-container {
  display: flex;
  align-items: center;
  gap: 20px; 
}

.profile-picture {
  width: 150px; 
  height: 150px; 
  border-radius: 50%; 
  object-fit: cover; 
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); 
}

a {
  text-decoration: none;
}